body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* SCROLL */
::-webkit-scrollbar { width: 5px; }
/* 스크롤바의 width */
::-webkit-scrollbar-track { background-color: #f0f0f0; }
/* 스크롤바의 전체 배경색 */
::-webkit-scrollbar-thumb { 
    background: linear-gradient(to bottom, #1697bf, #62b7ac); 
}
/* 스크롤바 색 */
::-webkit-scrollbar-button { display: none; }
/* 위 아래 버튼 (버튼 없애기를 함) */


/* 책 이름 */
.book-name{
    text-align: center;
    font-size: 40px;
}

.book-nav {
    position: absolute;
    background-color: #e6e7e5;
    top: 0;
    left: 0;
    height: 100%;
    width: 300px;
    overflow-y:auto;
}

li,ol,ul{
    margin :0;
    display: block;
    padding-left: 25px;
    text-align :left;
    height:30px;
    line-height: 30px;
    color: #a6aa9c;
}

.book-nav-chapter{
    cursor : pointer;
    font-size: 15px;
}

.book-nav-chapter:hover {
    color: #000000;
    background-color: #f7d3ba;
    border-left: 2px solid #cb3837;
}


.book-nav-chapter-active {
    font-size: 20px;
    color: #000000;
    background-color: #f7d3ba;
    border-left: 2px solid #cb3837;
}

.book-nav-article{
    padding-left:50px;
    cursor : pointer;
    font-size: 10px;
}

.book-nav-article:hover{
    padding-left:50px;
    color: #000000;
    background-color: #f7d3ba;
    border-left: 2px solid #cb3837;
}

.book-nav-article-active{
    padding-left:50px;
    font-size: 13px;
    color: #000000;
    background-color: #f7d3ba;
    border-left: 2px solid #cb3837;
}

a{
    text-decoration: none;
}

.nav-line{
    height: 1px;
    margin: 5px 7px;
    overflow: hidden;
    background: rgba(0,0,0,.07);
  }
.cleaner {
    cursor: pointer;
}


.cleaner-left{
    background-color: rgba(0, 0, 0, 0);
    border: none; 
    position: absolute;
    top: 50%;
    left: 10px;
}

.cleaner-left:focus{
    outline: none;
}

.cleaner-right{
    background-color: rgba(0, 0, 0, 0);
    border: none; 
    position: absolute;
    top:50%;
    right: 10px;
}

.cleaner-right:focus{
    outline: none;
}
.content{
    margin: 0 5% 0 5%;
    width:90%;
    overflow-x: hidden;
}

.reply-form{
    width: calc(100% - 14px);
    height: 70px;
    border : 1px #c9c9c9 solid;
    border-radius: 10px;
}

.reply-form:focus{
    outline: none;
}

.reply-submit{
    color : #000000;
    background-color: #f7d3ba;
    border: none; 
    border-radius: 10px;
}

.reply-submit:hover{
    background-color: #fdb683;
    outline: none;
}

.reply-submit:focus{
    outline: none;
}
.book-wrapper{
    position: absolute;
    background-color: #f5efe3;
    width: calc(100% - 300px);
    top: 0;
    height: 100%;
    left: 300px;
    overflow-y: auto;
}

.wrap-line{
    height: 1px;
    margin: 5px;
    overflow: hidden;
    background: rgba(0,0,0,.07);
  }
/* SCROLL */
::-webkit-scrollbar { width: 5px; }
/* 스크롤바의 width */
::-webkit-scrollbar-track { background-color: #f0f0f0; }
/* 스크롤바의 전체 배경색 */
::-webkit-scrollbar-thumb { 
    background: linear-gradient(to bottom, #1697bf, #62b7ac); 
}
/* 스크롤바 색 */
::-webkit-scrollbar-button { display: none; }
